.block
	display: block

.inline
	display: inline

// This is for align text to right
.text-right
	text-align: right

// This is for align text to center
.text-center
	text-align: center

// This is for align text to justify
.text-justify
	text-align: justify

// This is for hide titles
.hidden-title,
.hidden-paragraph
	left: -9999px
	position: absolute
	text-indent: -9999px